nav{
    background: rbga(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 1rem 2rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1.2rem;
    border: 0.1rem solid black;
    border-radius: 3rem;
    backdrop-filter: blur(20px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: #fff;
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}
nav a.active{
    background: var(--color-primary);
    color: black;
}