#about{
    width: 100%;
    height: max-content;
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
    );
    display: grid;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0deg);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,13rem);
    gap: 1.5rem;
}
.about__card{
    background: transparent;
    border: 5px solid var(--color-bg-variant);
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    backdrop-filter: blur(10px);
}
.about__card:hover{
    background:var(--color-bg-variant);
    
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5{
    font-size: 1.2rem;
    color: #fff;
}
.about__card small{
    font-size: 0.8rem;
    color: #fff;
}
.about__content{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
.about__content p{
    margin: 1.5rem 0 1.5rem;
    font-size: 1.3rem;
    font-weight: 400;
    color: #fff;
    word-spacing: 0.3rem;
   text-shadow: 0 0 1rem black;
}
.btn{
    font-size: 1.7rem;

}
/* medium device */
@media screen and (max-width: 1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
  }
  /* small device */
  @media screen and (max-width: 600px){
      .about__me{
          width: 65%;
          margin: 0 auto 3rem;
      }
      .about__cards{
          grid-template-columns: 1fr 1fr;
          gap: 1rem;
      }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }

  }





















